@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.node {
  width: 25px;
  height: 25px;
  outline: 1px solid #181717;
  background-color: rgb(255, 255, 255, 0.4);
  display: inline-block;
}

.node:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: rgb(24, 23, 23);
}

.node-finish {
  background-color: rgb(255, 65, 65);
}

.node-finish:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: rgb(255, 65, 65);
}

.node-start {
  background-color: rgb(91, 255, 146);
}

.node-start:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: rgb(91, 255, 146);
}

.move-start {
  background-color: rgb(255, 255, 255, 0.4);
}

.move-start:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: rgb(91, 255, 146);
}

.move-finish {
  background-color: rgb(255, 255, 255, 0.4);
}

.move-finish:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: rgb(255, 65, 65);
}

.node-visited {
  -webkit-animation: visitedAnimation 2s;
          animation: visitedAnimation 2s;
  background-color: rgb(255, 255, 255, 0.4);
}

.node-visited:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: rgb(24, 23, 23);
}

@-webkit-keyframes visitedAnimation {
  0% {
    /* transform: scale(0.2); */
    background-color: rgba(214, 214, 214, 0.75);
    /* border-radius: 50%; */
  }
  25% {
    /* transform: scale(0.4); */
    background-color: rgba(196, 196, 196, 0.75);
    /* border-radius: 35%; */
  }
  50% {
    /* transform: scale(0.6); */
    background-color: rgba(185, 185, 185, 0.75);
    /* border-radius: 25%; */
  }

  75% {
    /* transform: scale(0.8); */
    background-color: rgba(173, 173, 173, 0.75);
    /* border-radius: 10%; */
  }

  100% {
    /* transform: scale(1); */
    background-color: rgb(255, 255, 255, 0.4);
    /* border-radius: 0%; */
  }
}

@keyframes visitedAnimation {
  0% {
    /* transform: scale(0.2); */
    background-color: rgba(214, 214, 214, 0.75);
    /* border-radius: 50%; */
  }
  25% {
    /* transform: scale(0.4); */
    background-color: rgba(196, 196, 196, 0.75);
    /* border-radius: 35%; */
  }
  50% {
    /* transform: scale(0.6); */
    background-color: rgba(185, 185, 185, 0.75);
    /* border-radius: 25%; */
  }

  75% {
    /* transform: scale(0.8); */
    background-color: rgba(173, 173, 173, 0.75);
    /* border-radius: 10%; */
  }

  100% {
    /* transform: scale(1); */
    background-color: rgb(255, 255, 255, 0.4);
    /* border-radius: 0%; */
  }
}

.node-wall {
  background-color: rgb(24, 23, 23);
}

.node-shortest-path {
  -webkit-animation: shortestPath 1.5s;
          animation: shortestPath 1.5s;
  background-color: rgb(255, 254, 106);
}

.node-shortest-path:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: rgb(24, 23, 23);
}

@-webkit-keyframes shortestPath {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    background-color: rgb(255, 254, 106);
  }

  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(255, 254, 106);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(255, 254, 106);
  }
}

@keyframes shortestPath {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    background-color: rgb(255, 254, 106);
  }

  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(255, 254, 106);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(255, 254, 106);
  }
}

.grid {
  margin: 10% auto 3% auto;
}

:root {
  --primary: #39A9DB;
  --warning: #F9DC5C;
  --danger: #E84855;
  --success: #0F9D58;
  --white: #fdfdfd;
  --dark: #181717;
}

.btn {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  border-radius: 10px;
  cursor: pointer;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
  top: 50%;
  left: 50%;
}

.btn:hover {
  /* transform: translateY(-3px); */
  -webkit-transform: scale(1.05);/*Grows in size like Angry Birds button*/
  -moz-transform: scale(1.05);
  -ms-transform: scale(1.05);
  -o-transform: scale(1.05);
}

/* Button Colors and Styles */

.btn--primary--solid {
  background-color: #39A9DB;
  background-color: var(--primary);
  color: #fdfdfd;
  color: var(--white);
  border: none;
}

.btn--warning--solid {
  background-color: #F9DC5C;
  background-color: var(--warning);
  color: #181717;
  color: var(--dark);
  border: none;
}

.btn--danger--solid {
  background-color: #E84855;
  background-color: var(--danger);
  color: #fdfdfd;
  color: var(--white);
  border: none;
}

.btn--success--solid {
  background-color: #0F9D58;
  background-color: var(--success);
  color: #fdfdfd;
  color: var(--white);
  border: none;
}

.btn--primary--outline {
  background-color: transparent;
  border: 2px solid #39A9DB;
  border: 2px solid var(--primary);
  color: #39A9DB;
  color: var(--primary);
}

.btn--warning--outline {
  background-color: transparent;
  border: 2px solid #F9DC5C;
  border: 2px solid var(--warning);
  color: #F9DC5C;
  color: var(--warning);
}

.btn--danger--outline {
  background-color: transparent;
  border: 2px solid #E84855;
  border: 2px solid var(--danger);
  color: #E84855;
  color: var(--danger);
}

.btn--success--outline {
  background-color: transparent;
  border: 2px solid #0F9D58;
  border: 2px solid var(--success);
  color: #0F9D58;
  color: var(--success);
}

/* Button Size */

.btn--medium {
  padding: 10px 20px;
  font-size: 18px;
}

.btn--large {
  padding: 15px 30px;
  font-size: 20px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  top: 10%;
}

body {
  background-color: #181717;
}

.App {
  font-family: sans-serif;
  text-align: center;
}

.App button {
  margin: 10px;
}

